<template>
  <div
    :class="['btn-to-detail-body-wrap', isBottom ? 'is-bottom' : '']"
    ref="target"
  >
    <transition>
      <div v-if="isShow" class="btn-to-detail-body-inner">
        <button class="btn-bk-h48-line" @click="clickBtnBk">
          Select Your Size
        </button>
        <button class="btn-to-detail-body" @click="clickBtnToDetail"></button>
      </div>
    </transition>
  </div>
</template>

<script>
import { animateScroll } from '@/assets/js/ui-common.js'

export default {
  name: 'BtnToDetailBody',
  props: {
  },
  data () {
    return {
      isShow: false,
      isBottom: false,
      fixedHeaderHeight: 87,
    }
  },
  methods: {
    // 스크롤 이벤트
    windowScroll: function (e) {
      const poductDetailBody = document.querySelector('.product-detail-body')

      if (window.scrollY >= poductDetailBody.offsetTop - this.fixedHeaderHeight + poductDetailBody.clientHeight) {
        this.isShow = true
      } else {
        this.isShow = false
      }

      if (window.scrollY + window.innerHeight >= this.$refs.target.offsetTop) {
        this.isBottom = true
      } else {
        this.isBottom = false
      }
    },
    clickBtnBk: function () {
      this.scrollToDetailBody()
    },
    clickBtnToDetail: function () {
      this.scrollToDetailBody()
    },
    scrollToDetailBody: function () {
      const to = document.querySelector('.product-detail-body').offsetTop - this.fixedHeaderHeight
      animateScroll(to)
    },
  },
  created () {
    window.addEventListener('scroll', this.windowScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.windowScroll)
  },
}
</script>